@import url(https://fonts.googleapis.com/css?family=Montserrat:700|Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-size: 16px;
  background: white;
  font-family: "Open Sans", sans-serif;
  overflow-x: hidden;
  z-index: 0;
}

.App {
  text-align: center;
}

.App-header {
  position: relative;
  min-height: 100vh;
  flex-direction: column;
  align-items: center;
  color: black;
  top: 4em;
}
.heading-background {
  position: fixed;
  opacity: 0.05;
  font-size: 20em;
  left: 0.2em;
  top: -0.5em;
  z-index: -100;
  color: #9352ae;
}

header h1 {
  font-size: 1em;
  font-weight: 400;
  padding: 2em;
  text-align: center;
}
nav {
  position: fixed;
  z-index: 100;
  background-color: black;
  display: flex;
  width: 100%;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  top: 0em;
}
nav li {
  display: inline-block;
  flex-direction: row;
  cursor: pointer;
}
nav li .link {
  text-decoration: none;
  color: #ffffff;
  padding: 2em;
  padding-bottom: 1em;
  opacity: 1;
}
@media only screen and (max-width: 1200px) {
  nav li .link {
    text-decoration: none;
    color: #ffffff;
    padding: 1.5em;
    padding-bottom: 1em;
    opacity: 1;
  }
}
nav li .active {
  background-color: white;
  color: blueviolet;
}
nav li :hover {
  color: blueviolet;
}
nav .logo {
  border-radius: 50%;
  width: 1.5em;
  height: 1.5em;
}

.resumeButton {
  background: black;
  border: 0px;
  padding-top: 1em;
  padding-bottom: 1em;
  padding-left: 3em;
  padding-right: 1em;
  font-size: 0.3em;
  transition: all 0.2s ease-out;
  -webkit-transition: all 0.2s ease-out;
  -moz-transition: all 0.2s ease-out;
  -ms-transition: all 0.2s ease-out;
  -o-transition: all 0.2s ease-out;
  font-family: "Open Sans", sans-serif;
  text-decoration: none;
  z-index: 1;
}
.container {
  margin: 10em;
}

.header-title {
  font-family: "Montserrat", sans-serif;
  font-size: 4em;
  text-align: left;
}
.title-container {
  position: relative;
  margin-left: 10em;
  top: 0em;
  margin-right: 10em;
}
.resume-container {
  position: absolute;
  right: 1em;
  top: -15em;
}
.about {
  display: flex;
  flex-direction: row;
  margin-top: 4em;
}
.about-content {
  position: relative;
}
.about-content h1 {
  font-family: "Montserrat", sans-serif;
  font-size: 5em;
  text-align: left;
}
.about-content p {
  font-size: 2em;
  width: 20em;
  text-align: left;
}
.about img {
  margin-left: 2em;
}
.work {
  position: relative;
  display: flex;
  flex-direction: column;
  top: 8em;
  align-items: flex-start;
}
.work .heading {
  font-family: "Montserrat", sans-serif;
  font-size: 5em;
  text-align: left;
}
.work .work-content {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2em;
}

.project img {
  width: 30em;
  height: 30em;
}
.project img:hover {
  box-shadow: 0 10px 20px 0 #9352ae;
}

.project h1 a {
  color: black;
}
.project h1 a:hover {
  color: #9352ae;
}

.education {
  text-align: left;
  padding: 0.5em;
}
.education span {
  font-size: 2em;
}

.contact {
  position: relative;
  top: 10em;
  text-align: left;
}
.contact h1 {
  font-family: "Montserrat", sans-serif;
  font-size: 5em;
  text-align: left;
}
.amazing-color {
  color: #9352ae;
  opacity: 1;
}

.contact .email {
  color: black;
  font-size: 2em;
}
.contact li {
  display: inline-block;
  margin-top: 0;
  margin-bottom: 1em;
  margin-right: 1em;
  margin-left: 0;
}
.contact li a {
  text-decoration: none;
  color: black;
}
.footer {
  position: relative;
  top: 2em;
  padding: 1em;
}
.footer a {
  text-decoration: none;
  color: #2c59fb;
}

