.buttonDownload {
  display: inline-block;
  position: relative;
  color: white;
  background-color: black;
  color: white;
  z-index: 1;
}

.buttonDownload:before,
.buttonDownload:after {
  content: "";
  position: absolute;
  top: 40%;
  left: 10px;
}

/* Download box shape  */
.buttonDownload:before {
  width: 30px;
  height: 10px;
  border-style: solid;
  border-width: 0 4px 4px;
}

/* Download arrow shape */
.buttonDownload:after {
  width: 0;
  height: 0;
  margin-left: 10px;
  margin-top: -7px;
  border-style: solid;
  border-width: 8px 8px 0 8px;
  border-color: transparent;
  border-top-color: inherit;

  -webkit-animation: downloadArrow 2s linear infinite;

          animation: downloadArrow 2s linear infinite;
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}

.buttonDownload:before {
  border-color: white;
}

.buttonDownload:hover:before {
  border-color: #4cc713;
}
.buttonDownload:after {
  border-top-color: white;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}
.buttonDownload:hover:after {
  border-top-color: #4cc713;
}

/* keyframes for the download icon anim */
@keyframes downloadArrow {
  /* 0% and 0.001% keyframes used as a hackish way of having the button frozen on a nice looking frame by default */
  0% {
    margin-top: -7px;
    opacity: 1;
  }

  0.001% {
    margin-top: -15px;
    opacity: 0;
  }

  50% {
    opacity: 1;
  }

  100% {
    margin-top: 0;
    opacity: 0;
  }
}

@-webkit-keyframes downloadArrow {
  /* 0% and 0.001% keyframes used as a hackish way of having the button frozen on a nice looking frame by default */
  0% {
    margin-top: -7px;
    opacity: 1;
  }

  0.001% {
    margin-top: -15px;
    opacity: 0;
  }

  50% {
    opacity: 1;
  }

  100% {
    margin-top: 0;
    opacity: 0;
  }
}

* {
  box-sizing: border-box;
}

/* Float four columns side by side */
.column {
  width: 100%;
  padding: 0 10px;
  display: block;
  margin-bottom: 20px;
}

/* Remove extra left and right margins, due to padding in columns */
.row {
  display: grid;
  grid-template-rows: repeat(2, 0.5fr);
  grid-gap: 4em;
  padding-right: 5em;
}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}

/* Style the counter cards */
.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2); /* this adds the "card" effect */
  padding: 20px;
  text-align: left;
  background-color: #f1f1f1;
}

/* On mouse-over, add a deeper shadow */
.card:hover {
  box-shadow: 0 10px 20px 0 #9352ae;
}

.card .image {
  width: 100%;
  height: 100%;
}
.card h4 {
  font-family: "Montserrat", sans-serif;
  font-size: 2em;
  color: black;
}
.card p {
  font-size: 1em;
  color: black;
}
.card a {
  text-decoration: none;
  color: black;
}

/* Responsive columns - one column layout (vertical) on small screens */
@media screen and (max-width: 600px) {
  .column {
    width: 100%;
    display: block;
    margin-bottom: 20px;
    padding: 10px;
  }
  .card h4 {
    font-family: "Montserrat", sans-serif;
    font-size: 4em;
  }
  .card p {
    font-size: 3em;
  }
  .row {
    display: grid;
    grid-template-rows: repeat(2, 0.5fr);
    grid-gap: 5em;
    padding-right: 0em;
  }
  .card a {
    font-size: 2em;
  }
  .card {
    width: 85em;
    margin-right: 2em;
  }
}

body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

#ms-container {
  width: 100%;
  height: 100vh;
  background: #1c9cf2;
}

.ms-content {
  position: absolute;
  width: 200px;
  height: 200px;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  border: 20px solid blueviolet;
  border-radius: 100%;
}

.ms-content-inside {
  width: 100%;
  height: 100%;
  position: relative;
}

.ms-line-down-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transform: rotate(0deg);
  transition: transform 0s ease;
}
#ms-download {
  display: none;
}
#ms-download:checked ~ .ms-line-down-container {
  transform: rotate(-360deg);
  transition: transform 1.5s ease 1.25s;
}

.ms-line-down {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  margin: auto;
  width: 10px;
  height: 125px;
  background: black;
  border-radius: 100px;
  transition: height 0.5s ease;
}

#ms-download:checked ~ .ms-line-down-container .ms-line-down {
  height: 10px;
  top: -20px;
  -webkit-animation: ms-bounce 0.5s forwards 0.55s;
          animation: ms-bounce 0.5s forwards 0.55s;
}
#ms-download:checked ~ .ms-line-down-container .ms-line-down:before {
  content: "";
  position: absolute;
  top: -10px;
  left: -105px;
  right: 0;
  bottom: 0;
  width: 200px;
  height: 200px;
  border: 20px solid transparent;
  border-radius: 100%;
  -webkit-animation: ms-border-fill 0.5s forwards 3s;
          animation: ms-border-fill 0.5s forwards 3s;
}

.ms-line-point {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
